.educationModal {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 900px;
  height: 800px;
  background-color: white;
  padding: 20px;
  border: 1px solid black;
  box-shadow: 10px 10px 5px #888888;
}

.form-control:disabled,
.form-control:read-only {
  background-color: #fff;
  color: rgb(48, 79, 99) !important;
  opacity: 1;
}

